<template>
	<!--用户管理界面-->
	<div class="d-box v-box">
		<div class="headDiv">
			<div class="headerLeft">
				<span class="spanclass">用户姓名</span>
				<el-input placeholder="请输入用户姓名" v-model="head.RealName" clearable>
				</el-input>
			</div>
			<div class="headerLeft">
				<span class="spanclass">账号</span>
				<el-input placeholder="请输入账号" v-model="head.UserName" clearable>
				</el-input>
			</div>
			<div class="headerLeft">
				<span class="spanclass">账户状态</span>
				<el-select v-model="head.accountStatus" filterable placeholder="请选择账户状态">
					<el-option v-for="item in accountSystems" :key="item.id" :label="item.name" :value="item.id">
					</el-option>
				</el-select>
			</div>
			<div class="headerLeft">
				<span class="spanclass">创建时间</span>
				<el-date-picker class="timeclass" v-model="head.timeValue" type="daterange" start-placeholder="开始日期" end-placeholder="结束日期" :default-time="['00:00:00', '23:59:59']" value-format="yyyy-MM-dd HH:mm:ss">
				</el-date-picker>
			</div>
			<div class="headerLeft">
				<span class="spanclass">所属组织</span>
				<el-select v-model="head.supOrganId" filterable placeholder="请选择上级组织">
					<el-option v-for="item in supOrgans" :key="item.id" :label="item.name" :value="item.id">
					</el-option>
				</el-select>
			</div>
			<div class="clearfix">
				<div class="headRight">
					<SolidButton :type='"solid"' :text='"查询"' @click="queryClick"></SolidButton>
					<SolidButton :type='"hollow"' :text='"重置"' @click="resetClick"></SolidButton>
				</div>
			</div>
			<!--2、列表头部-->
			<div class="iconButton clearfix">
				<Button :type='add.type' :icon='add.icon' :text='add.text' @click="addClick">	
				</Button>
				<Button :type='out.type' :icon='out.icon' :text='out.text' @click="deleteClick">	
				</Button>
				<Button :type='edit.type' :icon='edit.icon' :text='edit.text' @click="editClick">	
				</Button>
				<Button :type='permission.type' :icon='permission.icon' :text='permission.text' @click="permissionClick">	
				</Button>
				<Button :type='password.type' :icon='password.icon' :text='password.text' @click="passwordClick">
				</Button>
			</div>
		</div>

		<div class="flexTable">
			<Table :data='rows' :tableName='tableName' @selection-change="handleSelectionChange" :currentPage1="currentPage" :num='pageTotal' @size-change="handleSizeChange" @current-change="handleCurrentChange"></Table>
		</div>

		<!--5、弹窗-->
		<el-dialog :title="popwindow.title" :visible.sync="popwindow.dialogFormVisible" width="600px" center>
			<el-form :model="popwindow" class="clearfix">

				<el-form-item :label-position="popwindow.labelPosition" label="姓名" :label-width="popwindow.LabelWidth">
					<el-input v-model="popwindow.RealName" auto-complete="off" placeholder="请输入用户姓名" clearable></el-input>
				</el-form-item>

				<el-form-item class="lastformItem" :label-position="popwindow.labelPosition" label="所属组织" :label-width="popwindow.LongLabelWidth">
					<el-select class="selectwidth" v-model="popwindow.supOrganId" filterable placeholder="请选择上级组织">
						<el-option v-for="item in supOrgans" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>

				<el-form-item style="clear: both;" :label-position="popwindow.labelPosition" label="账号" :label-width="popwindow.LabelWidth">
					<el-input v-model="popwindow.UserName" auto-complete="off" placeholder="请输入账号" clearable></el-input>
				</el-form-item>

				<el-form-item class="lastformItem" :label-position="popwindow.labelPosition" label="账户状态" :label-width="popwindow.LongLabelWidth">
					<el-select class="selectwidth" v-model="popwindow.accountStatus" filterable placeholder="请选择账号状态">
						<el-option v-for="item in accountSystems" :key="item.id" :label="item.name" :value="item.id">
						</el-option>
					</el-select>
				</el-form-item>

			</el-form>
			<div slot="footer" class="dialog-footer">
				<SolidButton :type='"hollow"' :text='"取消"' @click="popwindow.dialogFormVisible = false"></SolidButton>
				<SolidButton :type='"solid"' :text='"确定"' @click="popSureClick"></SolidButton>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	import Button from '../../button/Button.vue';
	import SolidButton from '../../SelfControls/solidButton/solidButton.vue';
	import Table from '../../table/Table.vue';

	export default {
		components: {
			Button,
			SolidButton,
			Table,
		},
		data() {
			return {
				//组件化
				add: {
					text: '新增',
					type: 'icon-add',
					icon: 'icon-add',
				},
				out: {
					text: '删除',
					type: 'icon-delete',
					icon: 'icon-delete',
				},
				edit: {
					text: '编辑',
					type: 'icon-edit',
					icon: 'icon-edit',
				},
				permission: {
					text: '权限管理',
					type: 'icon-permission',
					icon: 'icon-permission',
				},
				password: {
					text: '密码重置',
					type: 'icon-password',
					icon: 'icon-password',
				},
				//1、head头部
				head: {
					//6个上传的参数
					RealName: "",
					UserName: "",
					supOrganId: '',
					accountStatus: '',
					//时间选择
					timeValue: ""
				},
				//选择器
				selectType: '', //1，2代表head的账户状态和所属组织，3，4代表popwindow的账户状态和所属组织
				//上级组织select
				supOrgans: [],
				//账户状态select
				accountSystems: [{
					name: "启用",
					id: 0
				}, {
					name: "禁用",
					id: 1
				}],
				//3、main内容
				rows: [],
				multipleSelection: [], //列表选择的数组
				ids: "", //for循环后id的合集
				//表格对应的字段
				tableName: [{
						value: 'userName',
						name: "账号"
					},
					{
						value: 'realName',
						name: "姓名"
					},
					{
						value: 'orgName',
						name: '所属组织'
					},
					{
						value: 'statusName',
						name: '账户状态'
					}
				],
				//4、分页
				currentPage: 1, //当前页数
				pageRows: 10, //每页
				pageTotal: 0, //总数
				//5、弹窗
				popwindow: {
					//基本配置
					dialogFormVisible: false,
					labelPosition: 'left',
					title: '',
					LongLabelWidth: '70px',
					LabelWidth: '70px',
					//显示
					isHidden: "",
					isDisable: true,
					popType: '',
					//几个参数
					RealName: "",
					UserName: "",
					supOrganId: '',
					accountStatus: '',
				},
			}
		},
		methods: {
			loadData() {
				var self = this;
				var p = {
					pageNum: self.currentPage,
					pageRows: self.pageRows,
					//查询多的
					createTime: self.head.timeValue[0],
					endTime: self.head.timeValue[1],
					orgId: self.head.supOrganId,
					realName: self.head.RealName,
					status: self.head.accountStatus,
					userName: self.head.UserName,
				}
				//1、接口-列表
				self.$api.post('/user/select', p, r => {
					console.log(r);
					self.rows = r.repData.rows
					self.pageTotal = r.repData.total
				}, e => {
					console.log(e);
				})
			},
			requestDeptList() {
				var self = this;
				//2、接口-上级组织list
				var p = {}
				self.$api.post('org/doOrgList', p, r => {
					console.log('zhe', r);
					self.supOrgans = r.repData.list;
				}, e => {
					console.log(e);
				})
			},
			//1、head头部
			queryClick() {
				var self = this;
				//时间删除之后self.timeValue不存在不能用self.timeValue[0]，要给一个空值
				if(!self.head.timeValue) {
					self.head.timeValue = ""
				}
				self.loadData()
			},
			resetClick() {
				var self = this;
				//参数赋空
				self.head.timeValue = ''
				self.head.RealName = ''
				self.head.UserName = ''
				self.head.supOrganId = ''
				self.head.accountStatus = ''
				self.loadData()
			},
			//2、列表头部按钮
			deleteClick() {
				if(this.multipleSelection.length == 0) {
					this.$message({
						message: '未勾选数据',
						type: 'warning'
					});
				} else {
					this.$confirm('确定删除勾选数据吗？', '', {
						confirmButtonText: '是',
						cancelButtonText: '否',
						type: '',
						center: true
					}).then(() => {
						this.deleteMethod();
					}).catch(() => {});
				}
			},
			deleteMethod() {
				var self = this;
				//参数赋空
				self.ids = "";
				for(var i = 0; i < self.multipleSelection.length; i++) {
					self.ids += self.multipleSelection[i].id + ','
				}
				var p = {
					ids: self.ids.substring(0, self.ids.length - 1)
				}
				//3、接口-删除
				self.$api.post('user/delete', p, r => {
					console.log(r);
					this.$message({
						message: '删除成功',
						type: 'success'
					});
					self.loadData();
				}, e => {
					console.log(e);
				})
			},
			//新增按钮
			addClick() {
				var self = this;
				//窗口配置
				self.popwindow.dialogFormVisible = true;
				//区别
				self.popwindow.title = "新增员工信息";
				self.popwindow.popType = "add";
				//self.popwindow.isDisable = false;
				//参数取空
				self.popwindow.RealName = ''
				self.popwindow.UserName = ''
				self.popwindow.supOrganId = ''
				self.popwindow.accountStatus = ''
			},
			//编辑按钮
			editClick() {
				var self = this;
				if(self.multipleSelection.length == 1) {
					if(self.multipleSelection[0].status == 1) {
						this.$message({
							message: '账号已禁用不可编辑',
							type: 'warning'
						});
						return
					}
					//编辑窗口配置
					self.popwindow.dialogFormVisible = true
					//区别
					self.popwindow.popType = "edit";
					self.popwindow.title = "修改员工信息"
					self.popwindow.isHidden = "none"
					//self.popwindow.isDisable = true
					//参数传值
					self.popwindow.RealName = self.multipleSelection[0].realName;
					self.popwindow.UserName = self.multipleSelection[0].userName;
					self.popwindow.supOrganId = self.multipleSelection[0].orgId;
					self.popwindow.accountStatus = self.multipleSelection[0].status;
				} else {
					this.$message({
						message: '请勾选单条数据',
						type: 'warning'
					});
				}
			},
			//权限按钮
			permissionClick() {
				var self = this;
				if(self.multipleSelection.length == 1) {
					if(self.multipleSelection[0].status == 1) {
						this.$message({
							message: '账号已禁用不可分配权限',
							type: 'warning'
						});
						return
					}
					console.log(self.multipleSelection[0])
					this.$api.sSetObject('userData', self.multipleSelection[0]) //缓存然后去取
					this.$router.push({
						path: "/UserPermissAssign"
					})
				} else {
					this.$message({
						message: '请勾选单条数据',
						type: 'warning'
					});
				}
			},
			//重置密码按钮
			passwordClick() {
				var self = this;
				if(self.multipleSelection.length == 0) {
					this.$message({
						message: '未勾选数据',
						type: 'warning'
					});
					return
				}
				this.$confirm('确定重置密码？', '', {
					confirmButtonText: '确定',
					cancelButtonText: '取消',
					type: 'warning',
					center: true
				}).then(() => {
					this.passwordSureClick();
				}).catch(() => {
					this.$message({
						type: 'info',
						message: '取消保存'
					});
				});
			},
			passwordSureClick() {
				var self = this;
				//参数赋空
				self.ids = "";
				for(var i = 0; i < self.multipleSelection.length; i++) {
					if(self.multipleSelection[i].status == 1) {
						this.$message({
							message: '选中的数据中不可包含已禁用数据',
							type: 'warning'
						});
						return;
					}
					self.ids += self.multipleSelection[i].id + ','
				}
				var p = {
					ids: self.ids.substring(0, self.ids.length - 1)
				}
				//4、接口-密码重置
				self.$api.post('user/update/password', p, r => {
					console.log(r);
					this.$message({
						message: '密码重置成功',
						type: 'success'
					});
					self.loadData();
				}, e => {
					console.log(e);
				})
			},
			//5、弹窗
			//确定按钮
			popSureClick() {
				var self = this;
				if(self.popwindow.popType == "add") {
					//空值判断
					if(self.popwindow.RealName.length == 0) {
						this.$message({
							message: '请输入姓名',
							type: 'warning'
						});
						return
					}
					if(self.popwindow.UserName.length == 0) {
						this.$message({
							message: '请输入账号',
							type: 'warning'
						});
						return
					}
					if(self.popwindow.supOrganId.length == 0) {
						this.$message({
							message: '请选择所属组织',
							type: 'warning'
						});
						return
					}
					if(self.popwindow.accountStatus.length == 0) {
						this.$message({
							message: '请选择账户状态',
							type: 'warning'
						});
						return
					}
					//5、接口-新增
					var p = {
						orgId: self.popwindow.supOrganId,
						realName: self.popwindow.RealName,
						status: self.popwindow.accountStatus,
						userName: self.popwindow.UserName,
					}
					self.$api.post('user/add', p, r => {
						console.log(r);
						this.$message({
							message: '新增成功',
							type: 'success'
						});
						self.loadData();
						self.popwindow.dialogFormVisible = false;
					}, e => {
						console.log(e);
					})
				} else {
					//6、接口-编辑
					var p = {
						id: self.multipleSelection[0].id,
						realName: self.popwindow.RealName,
						userName: self.popwindow.UserName,
						orgId: self.popwindow.supOrganId,
						status: self.popwindow.accountStatus
					}
					self.$api.post('user/update', p, r => {
						console.log(r);
						this.$message({
							message: '修改成功',
							type: 'success'
						});
						self.loadData();
						self.popwindow.dialogFormVisible = false;
					}, e => {
						console.log(e);
					})
				}
			},
			//3、main内容
			//select行改变
			handleSelectionChange(val) {
				console.log(val)
				this.multipleSelection = val;
			},
			//点击行选中复选框
			toggleSelection(row) {
				var rows = [];
				rows.push(row);
				if(rows) {
					rows.forEach(row => {
						this.$refs.multipleTable.toggleRowSelection(row);
					});
				} else {
					this.$refs.multipleTable.clearSelection();
				}
			},
			//4、footer分页
			//每页多少条数据
			handleSizeChange(val) {
				var self = this;
				console.log("每页" + val);
				self.pageRows = val;
				self.loadData();
			},
			//当前第几页
			handleCurrentChange(val) {
				var self = this;
				console.log("当前页" + val);
				self.currentPage = val;
				self.loadData();
			}
		},
		created: function() {
			this.loadData();
			this.requestDeptList();
		},
	}
</script>

<style scoped="scoped">
	/*整体flex布局*/
	
	.d-box {
		display: -moz-box;
		/* OLD - Firefox 19- (buggy but mostly works) */
		display: -webkit-box;
		/* OLD - iOS 6-, Safari 3.1-6 */
		display: -webkit-flex;
		/* NEW - Chrome */
		display: -ms-flexbox;
		/* TWEENER - IE 10 */
		display: box;
		display: flexbox;
		display: flex;
		/* NEW, Spec - Opera 12.1, Firefox 20+ */
		height: 100%;
		width: 100%;
	}
	
	.v-box {
		-webkit-box-orient: vertical;
		-webkit-box-direction: normal;
		-moz-box-orient: vertical;
		-moz-box-direction: normal;
		-webkit-flex-direction: column;
		-moz-flex-direction: column;
		-ms-flex-direction: column;
		-o-flex-direction: column;
		flex-direction: column;
	}
	
	.flexTable {
		-webkit-box-flex: 1;
		-moz-box-flex: 1;
		-ms-flex: 1;
		flex: 1;
		position: relative;
	}
	
	.headDiv {
		padding: 20px;
		background: #fff;
		overflow: hidden;
		min-height: 55px;
		position: relative;
	}
	
	.tableDiv {
		width: 100%;
		height: 100%;
		position: absolute;
		overflow: auto;
	}
	
	.footerDiv {
		height: 40px;
		position: relative;
	}
	/*1、head头部*/
	/*head-left内容*/
	
	.headerLeft {
		width: auto;
		height: 40px;
		float: left;
		margin-right: 20px;
		padding-bottom: 20px;
	}
	
	.spanclass {
		display: inherit;
		line-height: 40px;
		margin-right: 8px;
		float: left;
	}
	
	.headerLeft .el-input,.headerLeft .el-select {
		width: 188px;
		height: 40px;
	}
	
	.timeclass{
		width:432px;
		height:40px;
	}
	/*head-right内容*/
	
	.headRight {
		float: right;
	}
	/*head按钮*/
	
	.iconButton {
		float: left;
		padding-top: 20px;
	}
	/*3、窗口*/
	
	.el-form-item {
		width: 49%;
		float: left;
	}
	
	.lastformItem {
		margin-left: 10px;
	}
	
	.selectwidth {
		width: 199.5px;
	}
</style>


